<template lang="pug">
    .tests__content
        .tests__header
            h4.tests__title Список всех тестов
            ul.tests__filter
                li.tests__filter-item
                    label.label__filter-tests Выберите предмет:
                    select(v-model="selectedSubject").tests__filter-select
                        option(value=0) Все
                        option(:value='item.id' v-for='item in subjects') {{item.name}}
                li.tests__filter-item
                    label.label__filter-tests(for='classes') Выберите класс:
                    select(v-model="selectedClass").tests__filter-select
                        option(:value='0') Все
                        option(:value='item' v-for='item in 12') {{item}}
                li.tests__filter-item
                    label.label__filter-tests Выберите язык:
                    select(v-model="selectedLanguage").tests__filter-select
                        option(value='') Все
                        option(value='Казахский') Казахский
                        option(value='Русский') Русский
        .tests__container-admin
            ul.tests__list
                .tests__item.names
                    .test__item-row
                        .test__item-row.names
                        .tests__number #
                        .tests__code Код теста
                        .tests__subject Предмет
                        .tests__class Класс
                        .tests__class Время (мин)
                        .tests__pass Выбрать
                swiper.swiper(:options="swiperOption")
                    swiper-slide(v-for='item in slidesNumber')
                        li.tests__item(v-for="test,idx in filteredTests" :key="test.id" v-if='(item-1)*steps <= idx && idx< item*steps')
                            available-test-item(
                                :idx="idx"
                                :test="test"
                                )
                    .swiper-pagination.swiper-pagination-bullets(slot="pagination")

</template>
<script>
import { mapState, mapActions } from "vuex";
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/css/swiper.css'
export default {
    components: {
        availableTestItem: () => import("../../admin/tests/available-test-item"),
        Swiper,
        SwiperSlide
    },
    created() {
        this.fetchAllTests(),
        this.fetchSubjects()
    },
    computed: {
        ...mapState ("user", {
        user: state => state.user}),
        ...mapState ("tests", {
            tests: state => state.tests}),
        ...mapState ("subjects", {
            subjects: state => state.subjects}),
        filteredTests: function() {
            return this.tests
            //filter by subject
            .filter(test => {
                return this.selectedSubject == 0 || test.group.subject.id== this.selectedSubject;
            })
            //filter by class
            .filter(test => {
                return this.selectedClass == 0 || test.group.groupClass== this.selectedClass;
            })
            //filter by language
            .filter(test => {
                return this.selectedLanguage == '' || test.group.language== this.selectedLanguage;
            })
        },
        slidesNumber: function(){
                return (Math.floor(this.filteredTests.length/this.steps)==this.filteredTests.length/this.steps)?Math.floor(this.filteredTests.length/this.steps):Math.floor(this.filteredTests.length/this.steps)+1
        }
    }, 
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdn.mathjax.org/mathjax/latest/MathJax.js?config=TeX-MML-AM_CHTML')
        document.head.appendChild(recaptchaScript)

    },
    methods: {
        ...mapActions("tests", ["fetchAllTests"]),
        ...mapActions("subjects", ["fetchSubjects"])
    },
    data () {
        return {
            steps: 4,
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true,
                    renderBullet(index, className) {
                    return `<span class="${className} swiper-pagination-bullet-custom">${index + 1}</span>`
                    }
                }
            },
            selectedSubject: 0,
            selectedClass: 0,
            selectedLanguage: ''
        }
    }
}
</script>
